    justify-content: flex-end; }
  .headerBottom .headerBottom__content {
    margin-top: 80px;
    flex-basis: 46.3%;
    display: grid;
    grid-template-columns: repeat(4, minmax(134px, auto));
    grid-template-rows: repeat(4, minmax(60px, 85px));
    grid-template-areas: "one two three four" "desc desc desc five" "desc desc desc six" "desc desc desc seven "; }
    .headerBottom .headerBottom__content img {
      width: 100%; }
